import React from 'react';
import { BrowserRouter as Router } from 'react-router-dom';
import routes, { renderRoutes } from './routes';
import { BASENAME } from './config/constant';

import SuperTokens, { SuperTokensWrapper
    , getSuperTokensRoutesForReactRouterDom } from "supertokens-auth-react";
// import EmailPassword from "supertokens-auth-react/recipe/emailpassword";
import Session from "supertokens-auth-react/recipe/session";
import ThirdPartyEmailPassword, {Google, Apple} from "supertokens-auth-react/recipe/thirdpartyemailpassword";

import * as reactRouterDom from "react-router-dom";

SuperTokens.init({
    appInfo: { // learn on https://supertokens.com/docs/emailpassword/appinfo
        appName: "Quiz",
        apiDomain: "http://localhost:3003",
        websiteDomain: "http://localhost:3004",
        apiBasePath: "",
        websiteBasePath: "/auth",
    }
    , recipeList: [        
        ThirdPartyEmailPassword.init({            
            signInAndUpFeature: {                
                providers: [                    
                    Google.init(),                   
                    Apple.init(),                
                ]            
            }   
        })        
        , Session.init()
    ]

});
/* const App = () => {return (
        <React.Fragment>
            <Router basename={BASENAME}>{renderRoutes(routes)}</Router>
        </React.Fragment>
    );}; */
const App = () => {
    return (
        <SuperTokensWrapper>
            <React.Fragment>
                <Router basename={BASENAME}>
                    {/*This renders the login UI on the /auth route*/}
                    {getSuperTokensRoutesForReactRouterDom(reactRouterDom)}
                    {/*Your app routes*/}
                    {renderRoutes(routes)}
                    </Router>
            </React.Fragment>
        </SuperTokensWrapper>
    );    
};
export default App;