import React, { useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { ListGroup, Dropdown, Media, Button } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import PerfectScrollbar from 'react-perfect-scrollbar';
import axios from 'axios';

import Filters from './Filters';
import { API_SERVER } from '../../../../config/constant';
import { LOGOUT, HELP_OPEN, HELP_CLOSE } from '../../../../store/actions';

import avatar1 from '../../../../assets/images/user/avatar-1.jpg';

import useWindowSize from '../../../../hooks/useWindowSize';

import {lang} from '../../../../lang/lang';

const NavRight = () => {
    const account = useSelector((state) => state.account);
    const estado = useSelector((state) => state.st)
    const dispatcher = useDispatch();

    const [listOpen, setListOpen] = useState(false);
    // const [helpOpen, setHelpOpen] = useState(false);

    const helpOpen = () => {
        if( !estado.helpOpen )
            dispatcher({ type: HELP_OPEN });
        else
            dispatcher({ type: HELP_CLOSE });
    };

    const handleLogout2 = () => {
        axios
            .post(API_SERVER + 'users/logout', {}, { headers: { Authorization: `${account.token}` } })
            .then(function (response) {
                // Force the LOGOUT
                //if (response.data.success) {
                dispatcher({ type: LOGOUT });
                //} else {
                //    console.log('response - ', response.data.msg);
                //}
            })
            .catch(function (error) {
                console.log('error - ', error);
            });
    };
    const handleLogout = () => {
        dispatcher({ type: LOGOUT });
    };

// Abre la ayuda, usar el contexto
    const windowSize = useWindowSize();
    // let dropdownRightAlign = false;
    let navItemClass = ['nav-item'];
    if( windowSize.width <= 575 ){
        navItemClass = [...navItemClass, 'd-none'];
    }
    const [isBasic, setIsBasic] = useState(false);


    return (
        <React.Fragment>
            <ListGroup as="ul" bsPrefix=" " className="navbar-nav ml-auto" id="navbar-right">
                <ListGroup.Item as="li" bsPrefix=" ">
                    <Dropdown>
                        <Dropdown.Toggle as={Link} variant="link" to="#" className="displayChatbox" onClick={() => helpOpen()}>
                            <i className="feather icon-help-circle mx-1" />
                        </Dropdown.Toggle>
                    </Dropdown>
                </ListGroup.Item>
                <ListGroup.Item as="li" bsPrefix=" ">
                    <Dropdown>
                        <Dropdown.Toggle as={Link} variant="link" to="#" className="displayChatbox" onClick={() => setListOpen(true)}>
                            <i className="icon feather icon-filter" />
                        </Dropdown.Toggle>
                    </Dropdown>
                </ListGroup.Item>
                <ListGroup.Item as="li" bsPrefix=" ">
                    <Dropdown className="drp-user">
                        <Dropdown.Toggle as={Link} variant="link" to="#" id="dropdown-basic">
                            <i className="icon feather icon-settings" />
                        </Dropdown.Toggle>
                        <Dropdown.Menu alignRight className="profile-notification">
                            <div className="pro-head">
                                <img src={avatar1} className="img-radius" alt="User Profile" />
                                <span>
                                    {lang.usuario}
                                </span>
                                <Link to="#" className="dud-logout" onClick={handleLogout} title={lang.logout}>
                                    <i className="feather icon-log-out" />
                                </Link>
                            </div>
                            <ListGroup as="ul" bsPrefix=" " variant="flush" className="pro-body">
                                <ListGroup.Item as="li" bsPrefix=" ">
                                    <Link to="#" className="dropdown-item">
                                        <i className="feather icon-user" /> {lang.profile}
                                    </Link>
                                </ListGroup.Item>
                                <ListGroup.Item as="li" bsPrefix=" ">
                                    <Link to="#" className="dropdown-item" onClick={handleLogout}>
                                        <i className="feather icon-log-out" /> {lang.logout}
                                    </Link>
                                </ListGroup.Item>
                            </ListGroup>
                        </Dropdown.Menu>
                    </Dropdown>
                </ListGroup.Item>
            </ListGroup>
            <Filters listOpen={listOpen} closed={() => setListOpen(false)} />
        </React.Fragment>
    );
};
export default NavRight;
