// action - state management
import { HELP_OPEN, HELP_CLOSE, CLEAR_FILTROS, ACTIVO_TP, UP_THEME } from './actions';
// , ZONA, OFICINA, ALERTAS_TP, LINEA_NEGOCIO, CLIENTE, LOCATARIO

export const initialState = {
    helpOpen: true,
    activoTp: null,
    zona: null,
    oficina: null,
    alertasTp: null,
    lineaNegocio: null,
    cliente: null,
    locatario: null,
    theme: null
};

//-----------------------|| REDUCER ||-----------------------//

const xReducer = (state = initialState, action) => {
    switch (action.type) {
        case UP_THEME: {
            const { theme } = action.payload;
            return {
                ...state,
                theme
            };
        }
        case HELP_OPEN: {
            return {
                ...state,
                helpOpen: true
            };
        }
        case HELP_CLOSE: {
            return {
                ...state,
                helpOpen: false
            };
        }
        case CLEAR_FILTROS: {
            return {
                ...state,
                activoTp: null,
                zona: null,
                oficina: null,
                alertasTp: null,
                lineaNegocio: null,
                cliente: null,
                locatario: null
            };
        }
        case ACTIVO_TP: {
            // const { isLoggedIn, user, token } = action.payload;
            const { activoTp } = action.payload;
            return {
                ...state,
                activoTp
            };
        }
        default: {
            return { ...state };
        }
    }
};
export default xReducer;